import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Button, Card, CardBody, CardGroup, Col, Container, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import axios from "axios";
import {isLoggedIn, login} from '../../../modules/auth';

class Login extends Component {
  state = {
    account: '',
    password: ''
  }

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }
  handleKeyPress = (e) => {
    if (e.key === "Enter") {
      this.login();
    }
  }
  login = async () => {
    axios.post("http://localhost:3001/account/login", {
      account: this.state.account,
      password: this.state.password
    })
      .then( res => {
        let data=res.data;
        if(data.error){
          switch(data.errorCode){
            case 1:
              alert("아이디를 입력해주세요.");
              window.location.reload();
              return;
            case 2:
              alert("비밀번호를 입력해주세요.");
              window.location.reload();
              return;
            case 3:
              alert("존재하지 않는 계정입니다.");
              window.location.reload();
              return;
            case 4:
              alert("비밀번호가 올바르지 않습니다.");
              window.location.reload();
              return;
            case 5:
              alert("DB연결 에러.");
              window.location.reload();
              return;  
            default:
              alert("잘못된 접근입니다.")
              window.location.reload()
              return
          }
        } else {
          login(data.account, data.loggedInState);
        }
        if(!isLoggedIn()) {
          window.location.reload();
        }
        else {
          window.location.replace("/");
        }
      }).catch( error => {
        console.log('failed', error)
      })
  };
  render() {
    return (
      <div className="app flex-row align-items-center" > 
        <Container >
          <Row className="justify-content-center">
            <Col md="8">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    
                      <h1>Login</h1>
                      <p className="text-muted">Sign In to your account</p>
                      <InputGroup className="mb-3">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-user"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="text" onChange={this.handleChange} placeholder="Username" autoComplete="username" name="account"/>
                      </InputGroup>
                      <InputGroup className="mb-4">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-lock"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="password" onChange={this.handleChange} placeholder="Password" autoComplete="current-password" name="password" 
                        onKeyPress={this.handleKeyPress}/>
                      </InputGroup>
                      <Row>
                        <Col xs="6">
                          <Button color="primary" className="px-4" onClick={this.login}>Login</Button>
                        </Col>
                        <Col xs="6" className="text-right">
                          <Button color="link" className="px-0">Forgot password?</Button>
                        </Col>
                      </Row>
                    
                  </CardBody>
                </Card>
                <Card className="text-white bg-primary py-5 d-md-down-none" style={{ width: '44%' }}>
                  <CardBody className="text-center">
                    <div>
                      <h2>Sign up</h2>
                      <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
                        labore et dolore magna aliqua.</p>
                      <Link to="/register">
                        <Button color="primary" className="mt-3" active tabIndex={-1}>Register Now!</Button>
                      </Link>
                    </div>
                  </CardBody>
                </Card>
              </CardGroup>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Login;
